<router-link class="rounded overflow-hidden bg-white shadow d-block position-relative" :to="`/restaurants/${restaurant.RestaurantID}`" :title="restaurant.RestaurantName"><img class="position-absolute link" src="@/assets/icons/link_icon.svg" alt="link icon"><img class="object-cover w-100" v-if="'PictureUrl1' in restaurant.Picture" :src="restaurant.Picture.PictureUrl1" :alt="restaurant.RestaurantName" height="163"><img class="object-cover w-100" v-else src="@/assets/images/illustration.png" alt="該資料沒有圖片" title="該資料沒有圖片" height="163"><div class="px-3 py-2"><h4 class="text-dark text-truncate">{{ restaurant.RestaurantName }}</h4><div class="vstack"><div class="open-time mb-2 text-truncate" :title="restaurant.OpenTime">{{ restaurant.OpenTime }}</div><div class="location me-3 text-truncate" :title="restaurant.Address">{{ restaurant.Address }}</div></div></div></router-link>